import { useDebounceCallback } from '@speedlo/hooks'
import React, { KeyboardEvent, useCallback, useRef, useState } from 'react'
import { FormGroup, Input, InputGroup } from 'reactstrap'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { SecondaryButton } from '../../common/Buttons'
import {
  useMOrderPromoAdd,
  useMOrderPromoRemove,
  useQOrderPromoCodes,
} from '../../graph/generated'
import { FPromoCode } from '../../graph/types'
import { i18n } from '../../i18n'

type TPromoCode = FPromoCode.Fragment

interface IProps {
  codes: ReadonlyArray<TPromoCode>
  onCodeAdd(codeInput: string): Promise<void>
  onCodeRemove(codeId: ID): void
  autoSubmitAfterTimeout?: number
}

const CodesPanel = styled.div`
  margin-top: 0.5rem;
  & button {
    margin-right: 0.5rem;
    font-size: 0.7rem;
  }
`

export function OrderPromoInputUI({
  codes,
  onCodeAdd,
  onCodeRemove,
  autoSubmitAfterTimeout = 2000,
}: IProps) {
  const inputRef = useRef<HTMLInputElement>(null)
  const [isValidating, setValidating] = useState(false)

  const addCode = useCallback(async () => {
    if (!inputRef.current || inputRef.current.disabled) {
      return
    }
    const code = inputRef.current.value.trim()
    if (code.length === 0) {
      return
    }
    setValidating(true)
    inputRef.current.disabled = true
    inputRef.current.value = i18n.t`Validating promo code ${code}...`
    try {
      await onCodeAdd(code)
      if (inputRef.current) {
        inputRef.current.value = ''
      }
    } catch (err) {
      if (inputRef.current) {
        inputRef.current.value = code
      }
    } finally {
      setValidating(false)
      if (inputRef.current) {
        inputRef.current.disabled = false
        inputRef.current.focus()
      }
    }
  }, [onCodeAdd])

  const onChange = useDebounceCallback(addCode, autoSubmitAfterTimeout)

  const onKeyDown = useCallback((ev: KeyboardEvent<HTMLInputElement>) => {
    if (ev.key === 'Enter') {
      addCode()
      ev.preventDefault()
    }
  }, [])

  return (
    <FormGroup>
      <InputGroup>
        <Input
          innerRef={inputRef}
          autoComplete="off"
          disabled={isValidating}
          placeholder={i18n.t`Promo codes`}
          onChange={onChange}
          onKeyDown={onKeyDown}
          onBlur={addCode}
          onPaste={addCode}
        />
      </InputGroup>
      <CodesPanel>
        {codes.map(({ id, code }) => (
          <SecondaryButton key={id} onClick={() => onCodeRemove(id)}>
            {`${code} ✖`}
          </SecondaryButton>
        ))}
      </CodesPanel>
    </FormGroup>
  )
}

const OrderPromoInput: React.FC = () => {
  const { order, cart } = useRoot()

  const { data, loading } = useQOrderPromoCodes({ orderId: order.orderId! })

  const [addCodeMutation] = useMOrderPromoAdd()

  const onCodeAdd = React.useCallback(async (code: string) => {
    if (!order.orderId) {
      return
    }
    const result = await addCodeMutation({
      code,
      orderId: order.orderId,
      withGoods: cart.withGoods,
    })
    if (result) {
      const { userErrors } = result.addOrderPromoCode
      if (userErrors.length) {
        order.submitFailed(userErrors)
      }
    }
  }, [])

  const [removeCodeMutation] = useMOrderPromoRemove()

  const onCodeRemove = React.useCallback((codeId: ID) => {
    if (order.orderId) {
      removeCodeMutation({
        codeId,
        orderId: order.orderId,
        withGoods: cart.withGoods,
      })
    }
  }, [])

  return (
    <OrderPromoInputUI
      codes={loading ? [] : data.order.promoCodes}
      onCodeAdd={onCodeAdd}
      onCodeRemove={onCodeRemove}
    />
  )
}

export default OrderPromoInput
