import React from 'react'

import { SecondaryButton } from '../../common/Buttons'
import { PaymentGateEnum } from '../../graph/generated'
import { IOrderFormState } from './OrderFormSetup'
import { MAX_LENGTH } from './OrderNoteInput'

interface IProps {
  onFill(values: IOrderFormState): void
}

export const OrderFormAutoFill: React.FC<IProps> = ({ onFill }) =>
  process.env.REACT_APP_STAGE !== 'prod' ? (
    <SecondaryButton
      style={{ marginBottom: '0.5rem' }}
      onClick={() => {
        const faker: Faker.FakerStatic = require('faker')
        faker.locale = 'cz'
        onFill({
          firstname: faker.name.firstName(),
          lastname: faker.name.lastName(),
          email: faker.internet.email(),
          phone: faker.phone.phoneNumberFormat(0).replace(/\s/g, ''),
          phonePrefix: '+420',
          note: faker.lorem.sentence().slice(0, MAX_LENGTH - 1),
          tips: '',
          payment: PaymentGateEnum.Gopay,
          agreements: [],
        })
      }}
    >
      Autofill form [DEV ONLY]
    </SecondaryButton>
  ) : null
