import { useApolloClient } from '@speedlo/graphql'
import { useDebounceCallback, useRouterContext } from '@speedlo/hooks'
import { useGoPay } from '@speedlo/payments'
import { autorun } from 'mobx'
import { useDisposable } from 'mobx-react-lite'
import React from 'react'
import { Form, IFormApi, IFormState } from 'react-form'
import { Form as StrapForm } from 'reactstrap'

import { useRoot } from '../../app/useRoot'
import {
  PaymentGateEnum,
  QOrderPaymentStatusDocument,
  QOrderPaymentStatusQuery,
  QOrderPaymentStatusVariables,
  useMSubmitOrder,
} from '../../graph/generated'
import { logOrder } from '../../helpers/logger'
import { RM } from '../../helpers/ramda'
import { useAlertModal } from '../../hooks/useAlertModal'
import { i18n } from '../../i18n'
import { TAgreement, TOrderModel } from '../../models/OrderModel'

interface IProps {
  render(
    formApi: IFormApi & IFormState<IOrderFormState>,
    isSubmitting: boolean,
  ): ReactNode
}

export interface IOrderFormState {
  firstname: string
  lastname: string
  email: string
  phonePrefix: string
  phone: string
  note: string
  tips: string
  payment: PaymentGateEnum
  agreements: TAgreement[]
}

function getDefaultFormValues(orderModel: TOrderModel): IOrderFormState {
  return {
    firstname: orderModel.firstname,
    lastname: orderModel.lastname,
    email: orderModel.email,
    phone: orderModel.phone,
    phonePrefix: orderModel.phonePrefix || orderModel.defaultPhonePrefix,
    note: orderModel.note,
    tips: orderModel.tips,
    payment: orderModel.payment as PaymentGateEnum,
    agreements: [],
  }
}

const debounceInterval = 1500

export const OrderFormSetup: React.FC<IProps> = ({ render }) => {
  const client = useApolloClient()
  const { order: orderModel, business } = useRoot()
  const { history } = useRouterContext()

  useDisposable(() =>
    autorun(() => {
      if (!orderModel.isVamInitialized) {
        orderModel.initVam()
      }
    }),
  )

  const [submitOrder, { loading: isSubmitting }] = useMSubmitOrder()

  const [paymentError, alertPaymentError] = useAlertModal()

  const { executeGoPayPayment, paymentLoading } = useGoPay(
    async (orderId = orderModel.orderId!) => {
      if (!orderId) {
        return null
      }
      orderModel.goPayStatusCheck(true)
      const result = await client.query<
        QOrderPaymentStatusQuery,
        QOrderPaymentStatusVariables
      >({
        query: QOrderPaymentStatusDocument,
        variables: {
          orderId,
        },
      })
      orderModel.goPayStatusCheck(false)
      return result.data.status
    },
    {
      onError(errors) {
        const phone = business.branch!.phone
        alertPaymentError(
          i18n.t`Payment status check has failed`,
          errors.length
            ? errors.join('<br />')
            : i18n.t`We were unable to verify status of your payment. Please contact operator using number ${phone}`,
        )
      },
      onFinish() {
        history.replace('/finish')
      },
    },
  )

  const onChange = useDebounceCallback(
    async (formState: IFormState<IOrderFormState>, formApi: IFormApi) => {
      logOrder('onChange', formState)
      await orderModel.updateVam(formState.values)
      formApi.setValue(
        'agreements',
        mergeFormAgreements(formState.values.agreements, orderModel.agreements),
      )
    },
    debounceInterval,
  )

  const onSubmit = React.useCallback(async (formState: IOrderFormState) => {
    orderModel.rememberFormValues(formState)

    const result = await submitOrder(orderModel.getSubmitVariables(formState))
    if (result) {
      const { userErrors, gopayUrl } = result.submitOrder
      if (userErrors.length) {
        orderModel.submitFailed(userErrors)
        return
      }
      if (orderModel.payment === PaymentGateEnum.Gopay && gopayUrl) {
        executeGoPayPayment(gopayUrl)
      } else {
        history.replace(`/finish`)
      }
    } else {
      orderModel.submitFailed()
    }
  }, [])

  return (
    <>
      {paymentError}
      <Form
        defaultValues={getDefaultFormValues(orderModel)}
        validateOnMount={true}
        onChange={onChange}
        onSubmit={onSubmit}
      >
        {formApi => (
          <StrapForm onSubmit={formApi.submitForm}>
            {render(formApi, isSubmitting || paymentLoading)}
          </StrapForm>
        )}
      </Form>
    </>
  )
}

function mergeFormAgreements(
  formValue: TAgreement[],
  agreements: TAgreement[],
) {
  return agreements.map(agreement => {
    const formAgreement = formValue.find(RM.propEq('id', agreement.id))
    if (formAgreement) {
      return { ...agreement, isConfirmed: formAgreement.isConfirmed }
    }
    return agreement
  })
}
