import { useFullComputed } from '@speedlo/hooks'
import { reaction } from 'mobx'
import { useDisposable, useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { useGInitialLocation } from '../../graph/generated'
import { GeoPoint } from '../../helpers/gps'
import { logMap } from '../../helpers/logger'
import { MapPanning } from './atoms/MapPanning'

export const DeliveryMapPanning: React.FC = () => {
  const { location, map } = useRoot()
  const { data } = useGInitialLocation()

  useDisposable(() =>
    reaction(
      () => map.clickPoint,
      clickPoint => {
        if (clickPoint) {
          location.withUserLocation(clickPoint)
          map.clearClickPoint()
        }
      },
    ),
  )

  const panToGps = useFullComputed(
    () => {
      if (location.geoPoint) {
        logMap('pan to user location', location.geoPoint)
        return location.geoPoint
      } else if (location.geolocationPoint) {
        logMap('pan to geolocation', location.geolocationPoint)
        return location.geolocationPoint
      } else if (data && data.initialLocation) {
        logMap('pan to initial location', data.initialLocation.gpsCoordinates)
        return data.initialLocation.gpsCoordinates as GeoPoint
      }
      logMap('no pan location available')
      return
    },
    [data],
  )

  return useObserver(() => <MapPanning gps={panToGps.get()} map={map} />)
}
